body{
    font-family: sans-serif;
    background: #eeeeee;
    margin: 0 0 70px 0;
}
header {
    background: #0e94d7;
    color:white;
    padding: 20px;
    font-size: 25px;
}
main {
    display: flex;
    flex-direction: column;
}
main > * {
    margin-top:10px;
}
h1{
    font-weight: normal;
    margin: 20px 0;
}
button{
    background: #0e94d7;
    color:white;
    font-size: 20px;
    border: none;
    border-radius: 5px;
    padding: 10px;
    line-height: 1em;
    cursor: pointer;
}
button[disabled]{
    background: #ddd;
    cursor: default;
}
textarea {
    height:300px;
    border:none;
    padding:10px;
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.2);
}
label{
    display:flex;
    width: 100%;
    margin-top:10px;
    border-radius: 5px;
    overflow: hidden;
}
label > span {
    display: flex;
    align-items: center;
    background: #0e94d7;
    color: white;
    padding: 0 10px;
}
label.invalid > span, span.invalid {
    background: #a33;
}
pre{
    padding:20px;
    background: white;
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.2);
}
footer {
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 10px;
    background: white;
    box-shadow: 0px 2px 8px rgba(0,0,0,0.2);
}
footer .container {
    display: flex;
    justify-content: space-between;
}
.container{
    max-width: 800px;
    margin:0 auto;
    display:flex;
    justify-content: space-between;
}
.toolbar {
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.addExample{
    color:#0e94d7;
    font-weight: bold;
    cursor: pointer;
}
.card {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
    border-radius: 5px;
    color:#aaaaaa;
    background: white;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.2);
    margin-bottom: 10px;
}
.card.todo {
    background: #feb;
}
.placeholders {
    display: flex;
}
.placeholders > div {
    font-weight: bold;
    margin-left: 10px;
}
.choices {
    display: grid;
    grid-template-columns: min-content auto;
    flex-grow: 1;
}
.choices > span {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background: #6aaed0;
    color: white;
    padding: 0 10px;
}
.choices > span.invalid {
    background: #d06a6a;
}
.autoexpand {
    position: relative;
    flex-grow: 1;
}
.autoexpand textarea {
    position:absolute;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
    width: 100%;
    height:100%;
    resize:none;
    border-radius: 0px;
    box-shadow: inset 0px 2px 4px rgba(0,0,0,0.2);
}
.autoexpand textarea, .autoexpand div {
    padding: 10px;
    font-size: 16px;
    font-family:sans-serif;
    border:none;
    line-height: 1.3em;
    white-space:pre-wrap;
    box-sizing:border-box;
    margin:0px;
}
.autoexpand div {
    word-break: break-all;
}
.autoexpand.invalid textarea{
    background: #fdd;
}